import { useState } from "react"; // Nhp hook useState t React
import axios from "axios"; // Nhp th vin axios  thc hin cc yu cu HTTP
import { useNavigate } from "react-router-dom"; // Nhp hook useNavigate t react-router-dom  iu hng gia cc route
import { toast } from "react-toastify"; // Nhp th vin toast  hin th thng bo
import "./Dangki.css"; // Nhp stylesheet cho component ny
function RegisterForm() {
  // nh ngha mt functional component c tn RegisterForm
  const [username, setUsername] = useState(""); // Khai bo mt state variable cho username v setter function ca n
  const [password, setPassword] = useState(""); // Khai bo mt state variable cho password v setter function ca n
  const navigate = useNavigate(); // Khai bo mt bin navigate s dng hook useNavigate
  const handleSubmit = async (e) => {
    // nh ngha mt asynchronous function c tn handleSubmit nhn vo mt object event lm tham s
    e.preventDefault(); // Ngn chn hnh vi submit mc nh ca form
    if (username === "" || password === "") {
      // Kim tra nu username hoc password trng
      toast.error("Vui lng nhp tn ng nhp v mt khu!"); // Hin th thng bo li s dng th vin toast
      return; // Thot khi function
    }
    try {
      const response = await axios.post("/api/register", {
        // Thc hin mt POST request n endpoint /api/register s dng th vin axios
        username, // Gi bin username lm trng "username" trong request body
        password, // Gi bin password lm trng "password" trong request body
      });

      navigate("/login"); // iu hng n route /login s dng bin navigate
      // Thm thng bo khi ng k thnh cng
      toast.success(`ng k ti khon thnh cng! Xin cho ${username}`);
    } catch (error) {
      toast.error("ng k ti khon tht bi");
    }
  };

  return (
    <form className="formLogOut" onSubmit={handleSubmit}>
      <div>
        <label>Username:</label>
        <input
          type="text"
          value={username}
          onChange={(e) => setUsername(e.target.value)}
        />
      </div>
      <div>
        <label>Password:</label>
        <input
          type="password"
          value={password}
          onChange={(e) => setPassword(e.target.value)}
        />
      </div>
      <button type="submit">Register</button>
    </form>
  );
}

export default RegisterForm;
