import { useContext } from "react";
import { CartContext } from "../../context/CartContext";
import "./Cart.css";
import { toast } from "react-toastify";

const Cart = () => {
  const { cart, addToCart } = useContext(CartContext);

  // x l xa sn phm khi gi hng
  const handleRemoveFromCart = (product) => {
    const newCart = cart.filter((item) => item.id !== product.id);
    toast.success(" xa sn phm thnh cng");
    addToCart(newCart);
  };

  // x l tng s lng sn phm trong gi hng
  const handleIncrement = (product) => {
    const newCart = [...cart];
    const index = newCart.findIndex((item) => item.id === product.id);
    newCart[index].quantity += 1;
    addToCart(newCart);
  };

  // x l gim s lng sn phm trong gi hng
  const handleDecrement = (product) => {
    const newCart = [...cart];
    const index = newCart.findIndex((item) => item.id === product.id);
    if (newCart[index].quantity > 1) {
      newCart[index].quantity -= 1;
      addToCart(newCart);
    }
  };

  // x l clear gi hng
  const handleClearCart = () => {
    toast.success(" clear sn phm thnh cng");
    addToCart([]);
  };

  // tnh tng gi tr ca gi hng
  const getTotalPrice = () => {
    return cart.reduce(
      (acc, curr) => acc + parseInt(curr.gia) * parseInt(curr.quantity),
      0
    );
  };

  return (
    <div className="cart">
      {cart.map((item) => {
        return (
          <div>
            <h1>{item.name}</h1>
          </div>
        );
      })}
      <div className="cart-items">
        {/* thng bo gi hng trng  */}
        {cart.length === 0 ? (
          <p>Your cart is currently empty.</p>
        ) : (
          cart.map((product) => (
            <div key={product.id} className="cart-item">
              <div className="cart-item-image">
                <img src={product.avatar} alt={product.ten_truyen} />
              </div>
              <div className="cart-item-info">
                <h4>{product.ten_truyen}</h4>
                <p>Price: {product.gia} VND</p>
                <p>Quantity: {product.quantity}</p>
                <div className="cart-item-buttons">
                  {/* // x l gim s lng sn phm */}
                  <button onClick={() => handleDecrement(product)}>-</button>
                  {/* // x l tng s lng sn phm */}
                  <button onClick={() => handleIncrement(product)}>+</button>
                  {/* // x l xa sn phm */}
                  <button onClick={() => handleRemoveFromCart(product)}>
                    Remove
                  </button>
                </div>
              </div>
            </div>
          ))
        )}
      </div>
      {cart.length > 0 && (
        <div className="cart-summary">
          <p>Total Price: {getTotalPrice()}.000.000 VND</p>
          <button onClick={handleClearCart}>Clear Cart</button>
        </div>
      )}
    </div>
  );
};

export default Cart;

