import "./Filter.css";

const Filter = ({
  filteredAuthor, // bin state cho filteredAuthor
  setFilteredAuthor, // hm setter cho filteredAuthor
  filteredCategory, // bin state cho filteredCategory
  setFilteredCategory, // hm setter cho filteredCategory
}) => {
  // hm x l khi thay i gi tr ca input tc gi
  const handleAuthorChange = (event) => {
    setFilteredAuthor(event.target.value); // cp nht gi tr ca filteredAuthor bng gi tr mi c nhp vo
  };
  // hm x l khi thay i gi tr ca input th loi
  const handleCategoryChange = (event) => {
    setFilteredCategory(event.target.value); // cp nht gi tr ca filteredCategory bng gi tr mi c nhp vo
  };

  return (
    <div className="filter">
      <label htmlFor="author">Tc Gi:</label>
      <input
        type="text"
        id="author"
        name="author"
        value={filteredAuthor}
        onChange={handleAuthorChange}
      />

      <label htmlFor="category">Th Loi:</label>
      <input
        type="text"
        id="category"
        name="category"
        value={filteredCategory}
        onChange={handleCategoryChange}
      />
    </div>
  );
};
export default Filter;
